import { RebateSDKConfig, CardDetails } from './types';

// Backend URL for fetching card details (can be overridden later)
// Use an explicit localhost URL so demos served from a different port can reach it.
const SERVER_URL = 'https://rebate-sdk-api-test.optisolbusiness.com/mock_server/card';

function createEl(tag: string, className?: string, innerHTML?: string) {
  const el = document.createElement(tag);
  if (className) el.className = className;
  if (innerHTML !== undefined) el.innerHTML = innerHTML;
  return el;
}


function applyStyles(el: HTMLElement, styles: Record<string, string | number>) {
  Object.assign(el.style, styles as any);
}

const DEFAULT_CARD: CardDetails = {
  cardholderName: 'John Doe',
  cardNumber: '4532 1234 5678 4242',
  cvv: '123',
  expiry: '01/30'
};

export class RebateSDK {
  private cfg: RebateSDKConfig;
  private container: HTMLElement | null = null;
  private rootEl: HTMLElement | null = null;

  constructor(cfg: RebateSDKConfig) {
    if (!cfg || !cfg.containerId) {
      throw new Error('RebateSDK: containerId is required');
    }
    this.cfg = cfg;
  }

  // Public API to mount the SDK UI
  async mount() {
    const container = document.getElementById(this.cfg.containerId);
    if (!container) throw new Error(`Container with id ${this.cfg.containerId} not found`);
    this.container = container;

    // Clear container
    container.innerHTML = '';

    // Root element
    const root = createEl('div', 'onbe-rebate-root');
    applyStyles(root, {
      fontFamily: 'Arial, sans-serif',
      width: '100%',
      maxWidth: '400px',
      margin: '0 auto'
    });

    // Placeholder for card
    const cardHolder = createEl('div', 'onbe-rebate-card-holder');
    root.appendChild(cardHolder);

    container.appendChild(root);
    this.rootEl = root;

    // Show loading, then terms & conditions
    await this.showTermsFlow();
  }

  private async showTermsFlow() {
    // Show initial loading
    this.showLoadingBar('Loading terms...');
    
    // Simulate fetching terms
    await new Promise(resolve => setTimeout(resolve, 800));
    
    // Hide loading and show terms
    this.hideLoadingBar();
    await this.showTermsAndConditions();
  }

  private async showTermsAndConditions(): Promise<void> {
    return new Promise((resolve) => {
      if (!this.rootEl) return;
      
      const holder = this.rootEl.querySelector('.onbe-rebate-card-holder') as HTMLElement;
      if (!holder) return;
      
      holder.innerHTML = '';
      
      const termsContainer = createEl('div', 'onbe-terms-container');
      applyStyles(termsContainer, {
        padding: '20px',
        maxWidth: '100%'
      });
      
      const title = createEl('h3', 'onbe-terms-title', 'Terms & Conditions');
      applyStyles(title, {
        fontSize: '18px',
        fontWeight: '600',
        margin: '0 0 12px 0',
        textAlign: 'center',
        color: '#333'
      });
      
      const description = createEl('p', 'onbe-terms-desc', 'To receive your refund as a virtual card, please review and accept our terms and conditions.');
      applyStyles(description, {
        fontSize: '14px',
        color: '#666',
        marginBottom: '16px',
        lineHeight: '1.6'
      });
      
      const linksBox = createEl('div', 'onbe-links-box');
      applyStyles(linksBox, {
        background: '#f9fafb',
        padding: '16px',
        borderRadius: '8px',
        marginBottom: '16px'
      });
      
      const linksText = createEl('p', 'onbe-links-text', 'By accepting, you agree to receive your refund as a virtual prepaid card. Please read our policies:');
      applyStyles(linksText, {
        fontSize: '13px',
        color: '#555',
        marginBottom: '12px',
        lineHeight: '1.5',
        margin: '0 0 12px 0'
      });
      
      const linksWrapper = createEl('div', 'onbe-links-wrapper');
      applyStyles(linksWrapper, {
        display: 'flex',
        gap: '16px',
        flexWrap: 'wrap'
      });
      
      const termsLink = createEl('a', 'onbe-terms-link', 'Terms of Service →');
      termsLink.setAttribute('href', '#');
      termsLink.setAttribute('target', '_blank');
      applyStyles(termsLink, {
        color: this.cfg.primaryColor || '#0b6cff',
        textDecoration: 'none',
        fontSize: '14px',
        fontWeight: '500'
      });
      
      const privacyLink = createEl('a', 'onbe-privacy-link', 'Privacy Policy →');
      privacyLink.setAttribute('href', '#');
      privacyLink.setAttribute('target', '_blank');
      applyStyles(privacyLink, {
        color: this.cfg.primaryColor || '#0b6cff',
        textDecoration: 'none',
        fontSize: '14px',
        fontWeight: '500'
      });
      
      linksWrapper.appendChild(termsLink);
      linksWrapper.appendChild(privacyLink);
      linksBox.appendChild(linksText);
      linksBox.appendChild(linksWrapper);
      
      const checkboxLabel = createEl('label', 'onbe-checkbox-label');
      applyStyles(checkboxLabel, {
        display: 'flex',
        alignItems: 'center',
        gap: '10px',
        marginBottom: '20px',
        cursor: 'pointer'
      });
      
      const checkbox = createEl('input', 'onbe-checkbox') as HTMLInputElement;
      checkbox.type = 'checkbox';
      applyStyles(checkbox, {
        width: '18px',
        height: '18px',
        cursor: 'pointer'
      });
      
      const checkboxText = createEl('span', 'onbe-checkbox-text', 'I have read and agree to the terms and conditions');
      applyStyles(checkboxText, {
        fontSize: '14px',
        color: '#333'
      });
      
      checkboxLabel.appendChild(checkbox);
      checkboxLabel.appendChild(checkboxText);
      
      const buttonsContainer = createEl('div', 'onbe-buttons-container');
      applyStyles(buttonsContainer, {
        display: 'flex',
        gap: '12px'
      });
      
      const declineBtn = createEl('button', 'onbe-decline-btn', 'Decline');
      applyStyles(declineBtn, {
        flex: '1',
        padding: '12px',
        background: '#fff',
        color: '#666',
        border: '2px solid #e0e0e0',
        borderRadius: '8px',
        fontSize: '15px',
        fontWeight: '600',
        cursor: 'pointer'
      });
      
      const acceptBtn = createEl('button', 'onbe-accept-btn', 'Accept');
      applyStyles(acceptBtn, {
        flex: '1',
        padding: '12px',
        background: this.cfg.primaryColor || '#0b6cff',
        color: 'white',
        border: 'none',
        borderRadius: '8px',
        fontSize: '15px',
        fontWeight: '600',
        cursor: 'not-allowed',
        opacity: '0.5'
      });
      (acceptBtn as HTMLButtonElement).disabled = true;
      
      // Checkbox change handler
      checkbox.addEventListener('change', () => {
        if (checkbox.checked) {
          (acceptBtn as HTMLButtonElement).disabled = false;
          applyStyles(acceptBtn, { cursor: 'pointer', opacity: '1' });
        } else {
          (acceptBtn as HTMLButtonElement).disabled = true;
          applyStyles(acceptBtn, { cursor: 'not-allowed', opacity: '0.5' });
        }
      });
      
      // Accept button handler
      acceptBtn.addEventListener('click', () => {
        if (!(acceptBtn as HTMLButtonElement).disabled) {
          holder.innerHTML = '';
          this.fetchAndRenderCard();
          resolve();
        }
      });
      
      // Decline button handler
      declineBtn.addEventListener('click', () => {
        holder.innerHTML = '';
        const declineMsg = createEl('div', 'onbe-decline-msg', 'You have declined the terms. Please refresh to try again.');
        applyStyles(declineMsg, {
          textAlign: 'center',
          padding: '20px',
          color: '#666',
          fontSize: '14px'
        });
        holder.appendChild(declineMsg);
      });
      
      buttonsContainer.appendChild(declineBtn);
      buttonsContainer.appendChild(acceptBtn);
      
      termsContainer.appendChild(title);
      termsContainer.appendChild(description);
      termsContainer.appendChild(linksBox);
      termsContainer.appendChild(checkboxLabel);
      termsContainer.appendChild(buttonsContainer);
      
      holder.appendChild(termsContainer);
    });
  }

  private async fetchAndRenderCard() {
    // Show loading state
    this.showLoadingBar('Loading your card...');
    
    try {
      const details = await this.fetchCardDetails();
      this.hideLoadingBar();
      this.renderCard(details);
    } catch (err) {
      console.error('RebateSDK: Failed to fetch and render card', err);
      this.hideLoadingBar();
      // Show error message in container
      if (this.rootEl) {
        const errorEl = createEl('div', 'onbe-error');
        applyStyles(errorEl, {
          color: '#d32f2f',
          padding: '12px',
          textAlign: 'center',
          fontSize: '14px'
        });
        errorEl.textContent = 'Unable to load card details. Please try again later.';
        this.rootEl.appendChild(errorEl);
      }
    }
  }

  private showLoadingBar(message: string = 'Loading your card...') {
    if (!this.rootEl) return;
    
    const loadingContainer = createEl('div', 'onbe-loading-container');
    applyStyles(loadingContainer, {
      padding: '24px',
      textAlign: 'center'
    });

    const loadingText = createEl('div', 'onbe-loading-text', message);
    applyStyles(loadingText, {
      fontSize: '14px',
      color: '#666',
      marginBottom: '16px',
      fontWeight: '500'
    });

    const progressBarBg = createEl('div', 'onbe-progress-bg');
    applyStyles(progressBarBg, {
      width: '100%',
      height: '6px',
      background: '#e0e0e0',
      borderRadius: '3px',
      overflow: 'hidden',
      position: 'relative'
    });

    const progressBar = createEl('div', 'onbe-progress-bar');
    applyStyles(progressBar, {
      width: '0%',
      height: '100%',
      background: `linear-gradient(90deg, ${this.cfg.primaryColor || '#0b6cff'}, ${this.cfg.secondaryColor || '#6ec1ff'})`,
      borderRadius: '3px',
      transition: 'width 0.3s ease',
      animation: 'onbe-progress 2s ease-in-out infinite'
    });

    // Add CSS animation
    const style = createEl('style');
    style.innerHTML = `
      @keyframes onbe-progress {
        0% { width: 0%; }
        50% { width: 70%; }
        100% { width: 100%; }
      }
    `;
    document.head.appendChild(style);

    progressBarBg.appendChild(progressBar);
    loadingContainer.appendChild(loadingText);
    loadingContainer.appendChild(progressBarBg);
    
    const holder = this.rootEl.querySelector('.onbe-rebate-card-holder') as HTMLElement;
    if (holder) {
      holder.appendChild(loadingContainer);
    }
  }

  private hideLoadingBar() {
    if (!this.rootEl) return;
    const holder = this.rootEl.querySelector('.onbe-rebate-card-holder') as HTMLElement;
    if (holder) {
      const loadingContainer = holder.querySelector('.onbe-loading-container');
      if (loadingContainer) {
        loadingContainer.remove();
      }
    }
  }

  private async fetchCardDetails(): Promise<CardDetails> {
    // Try fetching from backend SERVER_URL first
    try {
      const resp = await fetch(SERVER_URL, { method: 'GET', headers: { 'Accept': 'application/json' }, cache: 'no-store' });
      console.log('RebateSDK: fetch to', SERVER_URL, 'status', resp.status);
      if (resp.ok) {
        const data = await resp.json();
        console.log('RebateSDK: server response', data);
        // Normalize response into CardDetails shape with sensible fallbacks
        const nameFromCfg = this.cfg.customer?.firstName ? `${this.cfg.customer.firstName} ${this.cfg.customer.lastName || ''}`.trim() : undefined;
        return {
          cardholderName: data.cardholderName || nameFromCfg || DEFAULT_CARD.cardholderName,
          cardNumber: data.cardNumber || DEFAULT_CARD.cardNumber,
          cvv: data.cvv || DEFAULT_CARD.cvv,
          expiry: data.expiry || DEFAULT_CARD.expiry
        } as CardDetails;
      } else {
        console.warn('RebateSDK: server returned non-ok status', resp.status);
      }
    } catch (err) {
      console.warn('RebateSDK: failed to fetch from server, falling back to mock', err);
    }

    // Fallback: simulated/mock response (keeps demo working offline)
    await new Promise((r) => setTimeout(r, 600));
    const name = this.cfg.customer?.firstName
      ? `${this.cfg.customer.firstName} ${this.cfg.customer.lastName || ''}`.trim()
      : DEFAULT_CARD.cardholderName;

    return {
      ...DEFAULT_CARD,
      cardholderName: name
    };
  }

  private maskCardNumber(cardNumber: string): string {
    // Extract last 4 digits
    const last4 = cardNumber.replace(/\s/g, '').slice(-4);
    return `•••• •••• •••• ${last4}`;
  }

  private renderCard(details: CardDetails) {
    if (!this.rootEl) return;
    const holder = this.rootEl.querySelector('.onbe-rebate-card-holder') as HTMLElement;
    if (!holder) return;
    holder.innerHTML = '';

    const cardEl = createEl('div', 'onbe-card');
    applyStyles(cardEl, this.cardStyles());
    
    // Mask the card number for display on card
    const maskedNumber = this.maskCardNumber(details.cardNumber);

  // name row previously shown above the card image - will render inside the card below the number

    // Card image area
    const cardImage = createEl('div', 'onbe-card-image');
    applyStyles(cardImage, {
      marginTop: '12px',
      position: 'relative',
      width: '100%',
      paddingBottom: '63%', // This creates the proper aspect ratio (1.586:1)
      background: `linear-gradient(135deg, ${this.cfg.primaryColor || '#0b6cff'}, ${this.cfg.secondaryColor || '#6ec1ff'})`,
      borderRadius: '12px',
      color: '#fff',
      boxSizing: 'border-box',
      overflow: 'hidden'
    });

    // Inner container for content with absolute positioning
    const cardContent = createEl('div', 'onbe-card-content');
    applyStyles(cardContent, {
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      padding: '16px',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'flex-start'
    });
    cardImage.appendChild(cardContent);

    // Company name inside top-left of the card image (if provided)
    if (this.cfg.companyName) {
      const companyInCard = createEl('div', 'onbe-company-in-card', this.cfg.companyName);
      applyStyles(companyInCard, {
        position: 'absolute',
        top: '12px',
        left: '16px',
        fontSize: '14px',
        fontWeight: '700',
        color: 'rgba(255,255,255,0.95)'
      });
      cardImage.appendChild(companyInCard);
    }

    // icons removed per request — no visual icon will be rendered on the card

    // Middle area for card number (masked)
    const numberEl = createEl('div', 'onbe-card-number', maskedNumber);
    applyStyles(numberEl, {
      fontSize: '20px',
      letterSpacing: '2px',
      fontFamily: 'monospace',
      fontWeight: '500',
      whiteSpace: 'nowrap',
      textAlign: 'left',
      margin: '74px 0 8px 16px', // top right bottom left -> left offset matches card padding
      overflow: 'hidden',
      textOverflow: 'ellipsis'
    });
    cardContent.appendChild(numberEl);

    // Cardholder name inside the card, right-justified and below the number
    const nameInside = createEl('div', 'onbe-cardholder-name', details.cardholderName);
    applyStyles(nameInside, {
      fontSize: '14px',
      fontWeight: '600',
      color: 'rgba(255,255,255,0.95)',
      textAlign: 'left',
      marginTop: '6px',
      marginLeft: '16px'
    });
    cardContent.appendChild(nameInside);

    // Bottom area for metadata
    const metaRow = createEl('div', 'onbe-card-meta');
    applyStyles(metaRow, { 
      display: 'flex', 
      justifyContent: 'space-between',
      alignItems: 'flex-end',  // This ensures bottom alignment
      marginTop: 'auto'
    });
    
    const expiry = createEl('div', 'onbe-card-expiry');
    applyStyles(expiry, { 
      display: 'flex',
      flexDirection: 'column',
      gap: '1px'
    });
    expiry.innerHTML = `
      <div style="color:rgba(255,255,255,0.7);font-size:9px;text-transform:uppercase;margin-bottom:1px">Exp</div>
      <div style="font-size:13px">${details.expiry || ''}</div>
    `;

    const cvv = createEl('div', 'onbe-card-cvv');
    applyStyles(cvv, { 
      textAlign: 'right',
      display: 'flex',
      flexDirection: 'column',
      gap: '1px'
    });
    cvv.innerHTML = `
      <div style="color:rgba(255,255,255,0.7);font-size:9px;text-transform:uppercase;margin-bottom:1px">CVV</div>
      <div style="font-size:13px">•••</div>
    `;

  metaRow.appendChild(expiry);
  metaRow.appendChild(cvv);
  // Put the metadata inside the card content so flex can bottom-align it
  cardContent.appendChild(metaRow);

    cardEl.appendChild(cardImage);

    // Collapsible info
    const toggle = createEl('button', 'onbe-collapse-toggle', 'Show details');
    applyStyles(toggle, {
      marginTop: '8px',
      background: 'transparent',
      border: 'none',
      color: this.cfg.primaryColor || '#0b6cff',
      cursor: 'pointer'
    });

    const detailsBox = createEl('div', 'onbe-details-box');
    applyStyles(detailsBox, {
      marginTop: '8px',
      padding: '8px',
      borderRadius: '6px',
      background: '#f7f7f7',
      display: 'none'
    });

    detailsBox.innerHTML = `
      <div><strong>Card Number:</strong> ${details.cardNumber}</div>
      <div><strong>CVV:</strong> ${details.cvv}</div>
      <div><strong>Expiry:</strong> ${details.expiry}</div>
    `;

    toggle.addEventListener('click', () => {
      if (detailsBox.style.display === 'none' || !detailsBox.style.display) {
        detailsBox.style.display = 'block';
        toggle.innerHTML = 'Hide details';
      } else {
        detailsBox.style.display = 'none';
        toggle.innerHTML = 'Show details';
      }
    });

    cardEl.appendChild(toggle);
    cardEl.appendChild(detailsBox);

    holder.appendChild(cardEl);
    
    // Add "Add to Wallet" button
    this.renderAddToWalletButton(holder);
  }

  private renderAddToWalletButton(holder: HTMLElement) {
    const walletContainer = createEl('div', 'onbe-wallet-container');
    applyStyles(walletContainer, {
      marginTop: '16px',
      display: 'flex',
      justifyContent: 'center'
    });

    const walletBtn = createEl('button', 'onbe-wallet-btn');
    applyStyles(walletBtn, {
      width: '100%',
      maxWidth: '320px',
      padding: '12px 16px',
      background: '#000',
      color: '#fff',
      border: 'none',
      borderRadius: '8px',
      fontSize: '15px',
      fontWeight: '600',
      cursor: 'pointer',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      gap: '8px',
      transition: 'background 0.2s'
    });

    // Wallet icon (generic wallet/card icon)
    const walletIcon = `
      <svg width="20" height="20" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M21 18v1c0 1.1-.9 2-2 2H5c-1.11 0-2-.9-2-2V5c0-1.1.89-2 2-2h14c1.1 0 2 .9 2 2v1h-9c-1.11 0-2 .9-2 2v8c0 1.1.89 2 2 2h9zm-9-2h10V8H12v8zm4-2.5c-.83 0-1.5-.67-1.5-1.5s.67-1.5 1.5-1.5 1.5.67 1.5 1.5-.67 1.5-1.5 1.5z" fill="currentColor"/>
      </svg>
    `;

    walletBtn.innerHTML = `${walletIcon}<span>Add to Wallet</span>`;

    walletBtn.addEventListener('mouseenter', () => {
      applyStyles(walletBtn, { background: '#333' });
    });

    walletBtn.addEventListener('mouseleave', () => {
      applyStyles(walletBtn, { background: '#000' });
    });

    walletBtn.addEventListener('click', () => {
      // For now, show a message. In production, this would integrate with Apple Wallet/Google Pay
      alert('Add to Wallet functionality will be integrated with Apple Wallet and Google Pay in production.');
    });

    walletContainer.appendChild(walletBtn);
    holder.appendChild(walletContainer);
  }

  private cardStyles() {
    return {
      background: 'transparent'
    } as Record<string, string | number>;
  }
}
